/* eslint-disable */
import react from 'react'
import { CheckboxGroup, Checkbox } from '@antmjs/vantui'

export default function Demo() {
  const [values, setValues] = react.useState(['a'])
  return (
    <CheckboxGroup
      direction="horizontal"
      value={values}
      max={2}
      onChange={(e) => {
        setValues([...e.detail])
      }}
    >
      <Checkbox name="a"> a</Checkbox>
      <Checkbox name="b"> b</Checkbox>
      <Checkbox name="c"> c</Checkbox>
    </CheckboxGroup>
  )
}
